import { onBeforeRouteUpdate, useRouter } from 'vue-router';
import { ref } from 'vue';
import { useStore } from 'vuex';
export default {
  name: "index",

  setup() {
    const router = useRouter();
    const store = useStore();
    const identity = ref(store.state.identity);
    const sPathName = ref(window.location.pathname); // console.log(sPathName)

    onBeforeRouteUpdate(to => {
      // console.log(from, to)
      sPathName.value = to.path;
    });

    function turnAccount() {
      router.push({
        path: '/setting/account'
      });
    }

    function turnProfile() {
      console.log(identity);

      if (identity.value == 1) {
        router.push({
          path: '/setting/profile/educator'
        });
      }

      if (identity.value == 2) {
        router.push({
          path: '/setting/profile/recruiter'
        });
      }
    }

    function turnPrivacy() {
      router.push({
        path: '/setting/privacy'
      });
    }

    return {
      sPathName,
      turnAccount,
      turnProfile,
      turnPrivacy,
      identity
    };
  }

};